.list{
    display: flex;
    width: 400px;
    padding: 5px;
    /* border: 1px solid red; */
    border-bottom: 1px solid #ccc;
    margin: 0 auto;
    justify-content: space-between;
    box-sizing: border-box;
}
.list .txt{
    display: flex;
    align-items: center;
    gap: 4px;
}
.list .txt input{
    width: 15px;
    height: 15px;
}

.list .btns{
    display: flex;
    align-items: center;
    gap: 5px;
    /* padding: 4px 0; */
    position: relative;
}
.list .btns span{
    position: absolute;
    left: -80px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 14px;
    color: #ccc;
}
.list .btns>button{
    color: #fff;
    border-radius: 10px;
    padding: 10px;
    background: #6395EC;
}
