.app{
    width: 400px;
    margin: 50px auto;
    /* text-align: left; */
    padding: 40px;
    border-radius: 15px;
    background: #fff;
    box-shadow: 0 0 15px rgba(0,0,0,0.5);
    position: relative;
}
.app>button{
    position: absolute;
    right: 50px;
    top: 70px;
    color: #fff;
    border-radius: 10px;
    padding: 10px 16px;
    background: #ff6b6b;
}