.formadd{
    display: flex;
    margin-top: 20px;
    gap: 10px;
}
.formadd>input{
    flex:1;
    padding: 10px;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.formadd>button{
    background: #6395EC;
    font-weight: bold;
    color: #fff;
    border-radius: 10px;
    padding: 10px;
}
.formadd>input:focus{
    border: none;
    border-bottom: 2px solid #6395EC;
}









